.form {
  display: flex;
  flex-direction: column;
  align-items: center;

  width: 400px;
  padding: 20px 15px;
  margin-bottom: 15px;

  background: rgb(102, 178, 178);
  background: linear-gradient(
    90deg,
    rgba(102, 178, 178, 1) 0%,
    rgba(178, 216, 216, 1) 100%
  );
  box-shadow: 10px 10px 24px 0px rgba(0, 0, 0, 0.45);
  border-radius: 3px;
}
